.item {
  display: inline-block;
  width: 100%;
  padding: 5px;
  color: var(--accent-color);
  border: 1px solid var(--main-color);
}

.item:hover {
  background-color: hsla(206, 27%, 56%, 0.3);
}

.item:not(:last-child) {
  border-bottom: none;
}

.option {
  font-weight: 700;
  text-transform: capitalize;
  margin-right: 5px;
}
